.PuzzleRealMain{
    display: flex;
    flex-direction: column;
    width: 350px;
    align-content: center;
    align-self: center;
    justify-content: center;
    justify-self: center;
    margin-top: -6em;
    transform: scale(0.8);
 }
 .PuzzleRealMain button{
     height: 2em;
     font-size: 1em;
     background-color: navy;
     cursor: pointer;
     color: white;
 }
 .PuzzleRealMain button:hover{
    height: 2em;
    background-color: gold;
    cursor: pointer;
    color: navy;
}
.PuzzleContainer{
   display: grid;
   grid-template-columns: 90px 90px 90px;
   grid-template-rows: 90px 90px 90px;
   list-style: none;
   grid-gap: 1px 1px;
   width: 270px;
}

.PuzzleContainer li {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: white;
    font-size: 2em;
    width: 90px;
    height: 90px;
    /* border: gray 2px solid; */
    cursor: pointer;
}
.PuzzleContainer li:hover
{
    width: 88px;
    height: 88px;
     border: white 2px solid; 

}
.PuzzleMainContainer{
    display: flex;
    background-color: black;
}



